const getTimeStamp = () => {
    const now = new Date();
    const timeString = now.toTimeString().substring(0, 8);
    const year = now.getFullYear();
    const month = now.getMonth() + 1;
    const day = now.getDate();

    return `${timeString} - ${day}/${month}/${year}`;
};

export { getTimeStamp };
